import React from "react"
import styled from "styled-components"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { Fade } from "react-slideshow-image"
import "./carousel.css"

const Picture = styled(Img)`
  width: 500px;
  overflow: hidden;
  z-index: 10;
  border-radius: 4px;
`
const Norway = styled(Picture)`
  width: 400px;
`
const CarouselContainer = styled.div`
  margin: 0 20px;
  width: 600px;
  overflow: hidden;
`

const getPicture = picture => (
  <StaticQuery
    query={graphql`
      query {
        maroko: file(relativePath: { eq: "maroko.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        maroko1: file(relativePath: { eq: "maroko1.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        norway: file(relativePath: { eq: "norway.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        stavanger: file(relativePath: { eq: "stavanger.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        akerBrygge: file(relativePath: { eq: "akerBrygge.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={picture}
  />
)

const fadeProperties = {
  duration: 6000,
  transitionDuration: 500,
  infinite: true,
  indicators: false,
  arrows: false,
  autoplay: true,
}

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
`

const Description = styled.p`
  margin: 50px;
  text-align: justify;
`

const getPictures = data => {
  return (
    <Fade {...fadeProperties}>
      <div className="each-fade">
        <div className="image-container">
          <Picture alt="maroko1" fluid={data.maroko1.childImageSharp.fluid} />
        </div>
      </div>

      <div className="each-fade">
        <div className="image-container">
          <Picture
            alt="stavanger"
            fluid={data.stavanger.childImageSharp.fluid}
          />
        </div>
      </div>

      <div className="each-fade">
        <div className="image-container">
          <Norway alt="norway" fluid={data.norway.childImageSharp.fluid} />
        </div>
      </div>

      <div className="each-fade">
        <div className="image-container">
          <Picture alt="maroko" fluid={data.maroko.childImageSharp.fluid} />
        </div>
      </div>

      <div className="each-fade">
        <div className="image-container">
          <Picture
            alt="akerBrygge"
            fluid={data.akerBrygge.childImageSharp.fluid}
          />
        </div>
      </div>
    </Fade>
  )
}

const carousel = () => {
  return (
    <Container>
      <CarouselContainer>{getPicture(getPictures)}</CarouselContainer>
      <Description>MEFKLm</Description>
    </Container>
  )
}

export default carousel
